/* eslint max-len: 0 */
import React from 'react';
import { BootstrapTable, TableHeaderColumn, ExportCSVButton } from 'react-bootstrap-table';


const products = [];

function addProducts(quantity) {
  const startId = products.length;
  for (let i = 0; i < quantity; i++) {
    const id = startId + i;
    products.push({
      id: id,
      name: 'Item name ' + id,
      price: 2100 + i
    });
  }
}

addProducts(100);

export default class CustomTable extends React.Component {
   handleExportCSVButtonClick = (onClick) => {
      // Custom your onClick event here,
      // it's not necessary to implement this function if you have no any process before onClick
      console.log('This is my custom function for ExportCSVButton click event');
      onClick();
    }

   createCustomExportCSVButton = (onClick) => {
      return (
        <ExportCSVButton
          btnText='CustomExportText'
          btnContextual='btn-danger'
          className='my-custom-class'
          btnGlyphicon='glyphicon-edit'
          onClick={ e => this.handleExportCSVButtonClick(onClick) }/>
      );
      // If you want have more power to custom the child of ExportCSVButton,
      // you can do it like following
      // return (
      //   <ExportCSVButton
      //     btnContextual='btn-warning'
      //     className='my-custom-class'
      //     onClick={ () => this.handleExportCSVButtonClick(onClick) }>
      //     { ... }
      //   </ExportCSVButton>
      // );
    }  

  render() {
   const selectRowProp = {
      mode: 'checkbox',
      bgColor: 'pink', // you should give a bgcolor, otherwise, you can't regonize which row has been selected
      hideSelectColumn: true,  // enable hide selection column.
      clickToSelect: true  // you should enable clickToSelect, otherwise, you can't select column.
    };
    const footerData = [
      [
        {
          label: 'Total',
          columnIndex: 0
        },
        {
          label: 'Total value',
          columnIndex: 2,
          align: 'right',
          formatter: (tableData) => {
            let label = 0;
            for (let i = 0, tableDataLen = tableData.length; i < tableDataLen; i++) {
              label += tableData[i].price;
            }
            return (
              <strong>{ label }</strong>
            );
          }
        }
      ]
    ];
    const options = {
      noDataText: '조회된 데이터가 없습니다.',
      //exportCSVBtn: this.createCustomExportCSVButton
      // withoutNoDataText: true, // this will make the noDataText hidden, means only showing the table header
    };

    return (
      <section className="section-container">
      <div class="section-content-container">
      <div class="section-content-wrap">
      <BootstrapTable
      data={ products }
      footerData={ footerData }
      footer         
      pagination
      selectRow={ selectRowProp }
      options={ options }
      exportCSV
      >
      <TableHeaderColumn dataField='id' isKey={ true }>Product ID</TableHeaderColumn>
      <TableHeaderColumn dataField='name'>Product Name</TableHeaderColumn>
      <TableHeaderColumn dataField='price'>Product Price</TableHeaderColumn>
      </BootstrapTable>
      </div>
      </div>
      </section>
    );
  }
}


